import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import Navbar from "../components/Navbar";
import GameCarousel from "../components/GameCarousel";
import { supabase } from "../lib/supabaseClient";

// Função para gerar preço fictício
const randomPrice = () => (Math.random() * 50 + 10).toFixed(2);

export default function Home() {
  const [heroGames, setHeroGames] = useState<any[]>([]);
  const [gamesByGenre, setGamesByGenre] = useState<{
    [genre: string]: { name: string; image: string; price: string; description: string }[];
  }>({});
  const [emBreve, setEmBreve] = useState<any[]>([]);
  const [selectedHero, setSelectedHero] = useState(0);
  const [searchTerm, setSearchTerm] = useState("");

  const genres = ["RPG", "Indie", "Ação", "Tiro", "MOBA"];

  useEffect(() => {
    async function fetchGames() {
      const { data, error } = await supabase.from("games").select("*");
      if (error) return console.error("Erro Supabase:", error);

      // Hero: pegar 5 jogos aleatórios
      setHeroGames(
        data
          .sort(() => 0.5 - Math.random())
          .slice(0, 5)
          .map((g) => ({
            name: g.title,
            image: g.image,
            price: randomPrice(),
            description: g.description,
          }))
      );

      // Agrupar por gênero
      const grouped: { [genre: string]: { name: string; image: string; price: string; description: string }[] } = {};
      genres.forEach((genre) => {
        grouped[genre] = data
          .filter((g) => g.category === genre)
          .map((g) => ({
            name: g.title,
            image: g.image,
            price: randomPrice(),
            description: g.description,
          }));
      });

      setGamesByGenre(grouped);

      // Em Breve: últimos 5 jogos adicionados
      setEmBreve(
        data
          .slice(-5)
          .map((g) => ({
            name: g.title,
            image: g.image,
            price: randomPrice(),
            description: g.description,
          }))
      );
    }

    fetchGames();
  }, []);

  // Filtrar por busca
  const filterGames = (games: any[]) =>
    games.filter((g) => g.name.toLowerCase().includes(searchTerm.toLowerCase()));

  return (
    <div className="bg-gray-900 text-white min-h-screen">
      {/* Navbar fixa */}
      <div className="fixed top-0 left-0 w-full z-50 bg-gray-900 shadow-md">
        <Navbar />
        {/* Barra de pesquisa */}
        <div className="max-w-7xl mx-auto px-6 py-2">
          <input
            type="text"
            placeholder="Procurar jogo..."
            className="w-full p-2 rounded-lg text-black"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
      </div>

      <main className="pt-32 max-w-7xl mx-auto px-6 space-y-24">
        {/* Hero Section */}
        {heroGames.length > 0 && (
          <section className="relative flex max-w-7xl mx-auto h-[500px] rounded-xl overflow-hidden shadow-lg">
            <div className="flex flex-col space-y-4 w-20 p-2">
              {heroGames.map((game, i) => (
                <img
                  key={i}
                  src={game.image}
                  alt={game.name}
                  className={`h-24 w-full object-cover rounded-lg cursor-pointer border-2 transition-all ${
                    selectedHero === i ? "border-blue-500" : "border-transparent"
                  }`}
                  onClick={() => setSelectedHero(i)}
                />
              ))}
            </div>
            <div className="flex-1 relative ml-6 rounded-lg overflow-hidden shadow-lg">
              <img
                src={heroGames[selectedHero].image}
                alt={heroGames[selectedHero].name}
                className="w-full h-full object-cover brightness-75"
              />
              <div className="absolute bottom-8 left-8">
                <h2 className="text-4xl sm:text-5xl font-bold">
                  {heroGames[selectedHero].name}
                </h2>
                <p className="mt-2 text-lg text-gray-300">
                  Preço: ${heroGames[selectedHero].price}
                </p>
                <p className="mt-1 max-w-md text-gray-300">{heroGames[selectedHero].description}</p>
                <Link
                  href="/games"
                  className="mt-4 inline-block bg-blue-600 hover:bg-blue-700 px-6 py-3 rounded-lg text-lg font-semibold transition-shadow shadow-lg"
                >
                  Jogar Agora
                </Link>
              </div>
            </div>
          </section>
        )}

        {/* Seções por gênero */}
        {genres.map(
          (genre) =>
            gamesByGenre[genre] &&
            filterGames(gamesByGenre[genre]).length > 0 && (
              <section key={genre}>
                <h2 className="text-3xl font-bold mb-6">{genre}</h2>
                <GameCarousel
                  title={genre}
                  games={filterGames(gamesByGenre[genre]).map((g) => ({
                    name: g.name,
                    image: g.image,
                    badge: `$${g.price}`,
                  }))}
                />
              </section>
            )
        )}

        {/* Mais Jogados */}
        {genres.map(
          (genre) =>
            gamesByGenre[genre] &&
            filterGames(gamesByGenre[genre]).length > 0 && (
              <section key={`top-${genre}`}>
                <h2 className="text-3xl font-bold mb-6">Mais Jogados - {genre}</h2>
                <GameCarousel
                  title={`Top ${genre}`}
                  games={filterGames(gamesByGenre[genre])
                    .slice(0, 5)
                    .map((g) => ({
                      name: g.name,
                      image: g.image,
                      badge: `$${g.price}`,
                    }))}
                />
              </section>
            )
        )}

        {/* Em Breve */}
        {emBreve.length > 0 && (
          <section>
            <h2 className="text-3xl font-bold mb-6">Em Breve</h2>
            <GameCarousel
              title="Em Breve"
              games={filterGames(emBreve).map((g) => ({
                name: g.name,
                image: g.image,
                badge: `$${g.price}`,
              }))}
            />
          </section>
        )}
      </main>
    </div>
  );
}
